.spanBlock {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
}

.span {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #0080FF;
}

.input {
  box-sizing: border-box;
  border: 1px solid #D1D1D1;
  border-radius: 5px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  padding: 8px 24px;
}

.pen {
  color: #0080FF;
}