.sidebar {
  position: fixed;
  top: 0;
  right: 100%;
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 320px;
  width: 100%;
  box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
  padding: 70px;
  z-index: 1000;
  background: #fff;
  transition: .5s ease all;
}

.sidebar.open {
  right: auto;
  left: 0;
  transition: .5s ease all;
}

.background {
  background-color: rgba(0,0,0,.3);
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 45px;
}

.nav a {
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: #000000;
  text-decoration: none;
}

.nav a:hover {
  color: #3D6B99;
}

.nav a.active {
  text-decoration: underline;
  color: #0059B2;
}
.close {
  position: absolute;
  top: 25px;
  right: 25px;
  display: inline-flex;
  cursor: pointer;
  border: none;
  background: #fff;
}

.close img {
  width: 20px;
}
